import React, { useState, useRef, useEffect } from "react";
import "./App.css";
import TodoList from "./TodoList";
import uuidv4 from "uuid/v4";

function App() {
  const [todos, setTodos] = useState([]);
  const inputRef = useRef();

  const LOCAL_STORAGE_KEY = "todoApp.todos";

  const handleAddTodo = () => {
    const newTodos = inputRef.current.value;
    if (newTodos.trim() == "") {
      alert("Todo can't be empty");
      return;
    }
    setTodos([...todos, { id: uuidv4(), name: newTodos, complete: false }]);
    todos.map(todo => {
      return console.log(todo.id);
    });
    inputRef.current.value = null;
  };

  useEffect(() => {
    const storedTodos = JSON.parse(localStorage.getItem(LOCAL_STORAGE_KEY));
    if (storedTodos) {
      setTodos(storedTodos);
    }
  }, []);

  useEffect(() => {
    localStorage.setItem(LOCAL_STORAGE_KEY, JSON.stringify(todos));
  }, [todos]);

  const toggleTodo = id => {
    console.log("id", id);
    const newTodos = [...todos];
    const todo = newTodos.find(todo => todo.id === id);
    todo.complete = !todo.complete;
    setTodos(newTodos);
  };

  const handleClearComplete = () => {
    setTodos(todos.filter(todo => !todo.complete));
  };

  const enterPressed = event => {
    var code = event.keyCode || event.which;
    if (code === 13) {
      handleAddTodo();
    }
  };

  let length = todos.filter(todo => !todo.complete).length;
  return (
    <div className="App">
      <span>TODO LIST</span>
      <TodoList todos={todos} toggleTodo={toggleTodo} />
      <div className="app-input-group">
        <input
          onKeyDown={enterPressed}
          ref={inputRef}
          type="text"
          placeholder="Enter Todo"
        />
        <button className="btn-primary" onClick={handleAddTodo}>
          Add Todo
        </button>
        <button className="btn-default" onClick={handleClearComplete}>
          Clear Complete
        </button>
      </div>
      <div>{length > 0 ? `${length} left to do` : `Add some tasks...`}</div>
    </div>
  );
}

export default App;
